___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: rgb(24, 19, 19);
}

body {
    background: linear-gradient(17deg, rgb(49, 127, 151) 0%, rgb(247, 247, 247) 100%);
    height: 100%;
    min-height: 100%;
    margin: 0px;
    padding: 0px;
}

html, body {
    min-height: 100% !important;
    max-width: auto;
    background-size: cover;
}

.weather-container > img {
    height: 120px;
    width: 120px;
}

.container {
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-items: center;
    align-items: center;
}

.weather-container {
    display: flex;
    flex-direction: column;
    min-width: 20%;
    gap: 10px;
    justify-items: center;
    align-items: center;
    border-radius: 20%;
    box-shadow: -20px 5px 20px rgb(73, 72, 72);
    width: fit-content;
    height: fit-content;
    padding: 20px;
    background: linear-gradient(17deg, rgba(179, 198, 214, 0.4) 0%, rgba(40, 168, 151, 0.4) 100%);
}

.hours {
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    justify-content: center safe;
    width: 80vw;
    gap: 20px;
    scrollbar-width: thin;
    padding: 20px;
}

body::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.3);
}

img {
    height: 80px;
    width: 80px;
}

.hour-card {
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: center;
    justify-items: center;
    box-shadow: -5px 5px 10px rgb(83, 79, 79);
    min-width: fit-content;
    border-radius: 5%;
    position: relative;
    top: 0;
    transition: top ease 0.5s;
    background: linear-gradient(17deg, rgb(179, 198, 214, 0.4) 0%, rgba(40, 168, 151, 0.4) 100%);
}

.hour-card > img {
    height: 20%;
}

.hour-card:hover {
    position: relative;
    top: -10px;
}

.tomorrow {
    display: flex;
    writing-mode: vertical-lr;
    font-weight: bold;
    font-size: large;
    text-transform: uppercase;
    flex-direction: column;
    text-orientation: upright;
    align-items: center;
    justify-items: center;
}

.precipprob::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    margin-right: 6px;
}
    
.temp::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,2DAA2D;IAC3D,sBAAsB;AAC1B;;AAEA;IACI,iFAAiF;IACjF,YAAY;IACZ,gBAAgB;IAChB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,2BAA2B;IAC3B,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,cAAc;IACd,SAAS;IACT,qBAAqB;IACrB,mBAAmB;IACnB,kBAAkB;IAClB,0CAA0C;IAC1C,kBAAkB;IAClB,mBAAmB;IACnB,aAAa;IACb,6FAA6F;AACjG;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,kBAAkB;IAClB,4BAA4B;IAC5B,WAAW;IACX,SAAS;IACT,qBAAqB;IACrB,aAAa;AACjB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,mBAAmB;IACnB,qBAAqB;IACrB,yCAAyC;IACzC,sBAAsB;IACtB,iBAAiB;IACjB,kBAAkB;IAClB,MAAM;IACN,yBAAyB;IACzB,4FAA4F;AAChG;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;IAChB,yBAAyB;IACzB,sBAAsB;IACtB,yBAAyB;IACzB,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,gDAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,gDAAsB;AAC1B","sourcesContent":["* {\r\n    box-sizing: border-box;\r\n    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n    color: rgb(24, 19, 19);\r\n}\r\n\r\nbody {\r\n    background: linear-gradient(17deg, rgb(49, 127, 151) 0%, rgb(247, 247, 247) 100%);\r\n    height: 100%;\r\n    min-height: 100%;\r\n    margin: 0px;\r\n    padding: 0px;\r\n}\r\n\r\nhtml, body {\r\n    min-height: 100% !important;\r\n    max-width: auto;\r\n    background-size: cover;\r\n}\r\n\r\n.weather-container > img {\r\n    height: 120px;\r\n    width: 120px;\r\n}\r\n\r\n.container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 30px;\r\n    justify-items: center;\r\n    align-items: center;\r\n}\r\n\r\n.weather-container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    min-width: 20%;\r\n    gap: 10px;\r\n    justify-items: center;\r\n    align-items: center;\r\n    border-radius: 20%;\r\n    box-shadow: -20px